<template>
  <div>
    <div class="wrapper" v-if="aDataIsEmpty">
      <ul class="timeline">
        <!-- <li>
       <h3 class="time">2018-10-08 <span>18:22:30</span></h3>
       <h3 class="time">2019-01-10 23:00:39</h3>
       <h4>王磊 13716927668 签收</h4>
       <p>广东省深圳市 中国北京市朝阳区姚家园路87号</p>
     </li> -->
        <li v-for="(item, i) in aData" :key="i">
          <h3 class="time">{{ item.operatedAt }}</h3>
          <h4>{{ item.status }} {{ item.addresses.driver_name }} {{ item.addresses.telephone }}</h4>
          <p>{{ item.addresses.province }} {{ item.addresses.city }} {{ item.addresses.address }}</p>
        </li>
        <!-- <li>
       <h3 class="time">08:22</h3>
       <h4>我是标题</h4>
       <p>我是内容我是内容我是内容我是内容我是内容</p>
     </li>
     <li>
       <h3 class="time">10.08日<br />08:22</h3>
       <h4>我是标题</h4>
       <p>我是内容我是内容我是内容我是内容我是内容</p>
     </li>
     <li>
       <h3 class="time">10.08日<br />08:22</h3>
       <h4>我是标题</h4>
       <p>我是内容我是内容我是内容我是内容我是内容</p>
     </li> -->
      </ul>

    </div>
    <div v-else class="nodata">无数据显示</div>
  </div>
</template>

<script>
  export default {
    // name: 'firstvvv',
    data() {
      return {
        aData: [],
        aTime: []
      }
    },
    computed: {
      aDataIsEmpty() {
        return this.aData.length
      }
    },
    // beforeMount(){
    // beforeUpdate(){
    created(){
    // mounted() {
      // this.$nextTick(() => {
        this.$axios.get('/static/json/trace.json').then((response) => {
          //请求成功
          console.log(response);
          if (response.status === 200) {
            this.aData = response.data
            // this.mergeTime()
          }
          console.log(this.aData);
        }).catch(function(error) {
          //请求失败
          alert('获取数据失败，请刷新页面！')
        });
      // })
    },
    methods: {
      mergeTime() {
        // 
        for (let i of aData) {
          aTime.push(i.operatedAt)
        }
        console.log('chulai')

        // this.aData
      }
    }
  }
</script>

<style scoped>
  @charset "utf-8";
  /* @import url("../assets/css/reset.css"); */
  @import url("../assets/css/base.css");

  html {
    font-size: 12px;
  }

  h3,
  h4 {
    font-weight: normal;
  }

  .timeline {
    width: 100%;
    height: auto;
    padding-bottom: 1rem;
  }

  .timeline li {
    position: relative;
    padding-left: 2rem;
    padding-bottom: 1rem;
  }

  .timeline li h4 {
    color: #272B3E;
    font-size: 1rem;
    margin-bottom: 0.8rem;
  }

  .timeline li h4:before {
    content: '';
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    left: 0;
    margin-left: -6px;
    top: 7px;
    background: #D7BB67;
    -moz-border-radius: 100%;
    -webkit-border-radius: 100%;
    border-radius: 100%;
    background-size: 10px 10px;
  }

  .timeline li h4:after {
    content: '';
    display: block;
    position: absolute;
    width: 2px;
    height: 100%;
    background: #D7BB67;
    left: 0;
    top: 0.6rem;
    margin-left: -2px;
  }

  .timeline li p {
    width: 100%;
    height: auto;
    background: #F4F4F4;
    padding: 0.8rem 1.5rem 1.5rem 0.8rem;
    color: #272B3E;
    font-size: 1rem;
    line-height: 140%;
  }

  .timeline li .time {
    position: absolute;
    left: -5.3rem;
    font-size: 1.2rem;
  }





  /* start 微调1 */

  .timeline li p {
    padding: 0.8rem 1.5rem 1rem 0.8rem;
  }

  .nodata {
    font-size: 1rem;
    text-align: center;
    margin-top: 1rem;
  }

  /* end 微调1 */


  /* start 适应大屏幕 */

  .timeline li .time {
    /* left: -13.3rem;
    width: 146px; */
    width: 90px;
    left: -8.5rem;
    text-align: right;
    top: 3px;
    /*  display: block;
  word-break: break-all;
  word-wrap: break-word; */
  }

  .wrapper {
    padding-left: 17%;
    background-color: #fff;
    padding-top: 1rem;
  }

  /* end */

  @media screen and (min-width:800px) {
    .wrapper {
      padding-left: 17%;
    }

    .timeline li .time {
      font-size: 1rem;
    }
  }

  @media screen and (max-width:370px) {
    .timeline li .time {
      width: 90px;
      left: -8.5rem;
    }

    .wrapper {
      padding-left: 34%;
    }
  }

  @media screen and (max-width:330px) {
    .timeline li .time {
      width: 90px;
      left: -8.5rem;
    }

    .wrapper {
      padding-left: 34%;
    }
  }
</style>



